/* custom select element styling */
/*  Remove default styling of select element  */
*::before, *::after {
  box-sizing: border-box;
}
select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: transparent;
  border: none;
  padding: 0 1rem 0 1rem;
  margin: 0;
  width: 100%;
  height: 32px;
  font-family: inherit;
  font-size: inherit;
  cursor: inherit;
  line-height: inherit;
  outline: none;
}

/*   New styling to Select  */
.customSelect {
  width: 100%;
  min-width: 15ch;
  border-radius: 0.25em;
  cursor: pointer;
  background-color: #fff;
  border: solid darkgrey 1px; 
  display: grid;
  grid-template-areas: "select";
  align-items: center;
  position: relative;
}

/*  Custom select-arrow  */
.customSelect::after {
  content: "";
  width: 0.8em;
  height: 0.5em;
  background-color: #303030;
  -webkit-clip-path: polygon(100% 0%, 0 0%, 50% 100%);
          clip-path: polygon(100% 0%, 0 0%, 50% 100%);
  justify-self: end;
  margin-right: 1rem;
}

select,
.customSelect:after {
    grid-area: select;
}

/*  Select focus state  */
select:focus + .focus {
  position: absolute;
  top: -1px;
  left: -1px;
  right: -1px;
  bottom: -1px;
  border: 2px solid #2C7CAE;
  border-radius: inherit;
}


